import { useState } from "react";
import axios from "axios";
const HomeScreen = () => {
  const [text, settext] = useState([]);
  const [short_str, setshortStr] = useState("");
  function CopyToClipboard(id) {
    const str = document.getElementById(id).innerText;
    const el = document.createElement("textarea");
    el.value = str;
    el.setAttribute("readonly", "");
    el.style.position = "absolute";
    el.style.left = "-9999px";
    document.body.appendChild(el);
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
  }

  const createShortLink = () => {
    if (text.length > 8) {
      axios
        .post("/api/", {
          original_url: text,
        })
        .then(function (response) {
          setshortStr(response.data.short_char);
        })
        .catch(function (error) {
          console.log(error);
        });
    }
  };

  return (
    <div className="header p-5">
      <div className="text-center mx-auto box mx-3">
        <h3 className="heading pt-5 m-3">Shorten URL Is Just Simple</h3>
        <input
          placeholder="https://mail.google.com/"
          type="text"
          className="form-control url  mx-auto"
          value={text}
          onChange={(e) => settext(e.target.value)}
        />
        <button className="button mt-3" onClick={() => createShortLink()}>
          Get Link
        </button>
        {short_str.length > 5 && (
          <div className="link-box row w-75 mx-auto my-3">
            <div className="col p-2">
              <span id="copy-text">
                <a
                  href={`https://trimp.pythonanywhere.com/${short_str}/`}
                >{`https://trimp.pythonanywhere.com/${short_str}/`}</a>
              </span>
            </div>
            <div className="col p-2">
              <a
                href="#copied"
                className="button copy"
                onClick={() => CopyToClipboard("copy-text")}
              >
                copy
              </a>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default HomeScreen;
